<template>
  <lay-header
    :showBack="true"
    :tBarColor="'#FFFFFF'"
    :txtTitle="selectTag ? selectTag.title : ''"
    v-if="active"
  >
    <v-layout column class="pt-2 px-5 pb-10 lay-body">
      <c-list-item
        v-for="i in 10"
        :key="i"
        :value="'[미니] 미니 전원이 안켜져요/ 미니 연결이 안돼요'"
        :onClick="
          () => {
            pushRouter({ name: 'faqDetail' });
          }
        "
      />
      <span class="txt-others mt-12 mb-4">다른 내용이 궁금하세요?</span>
      <v-layout
        class="lay-others mt-2"
        align-center
        v-for="item in tags"
        :key="item.seq"
        v-ripple
        @click.prevent.stop="
          () => {
            selectTag = item;
            $vuetify.goTo(0, {
              duration: 150,
              offset: 0,
              easing: 'easeInOutCubic',
            });
          }
        "
        v-show="item != selectTag"
      >
        <img class="ic" :src="item.ic" />
        <span class="txt">{{ item.title }}</span>
        <v-spacer />
        <img class="ic-arr" src="@/assets/images/ic-arrow-r-b-16.svg" />
      </v-layout>

      <c-btn
        class="mt-14"
        :thema="'disable'"
        :height="44"
        :py="12"
        :fontSize="15"
        :onClick="() => pushRouter({ name: 'qnaReg' })"
        >1:1 문의하기</c-btn
      >
    </v-layout>
  </lay-header>
</template>

<script>
import cListItem from "@/components/cListItem.vue";
export default {
  name: "KeepAlive",
  components: { cListItem },
  props: ["seq"],
  data() {
    return {
      selectTag: null,
      active: false,
      tags: [
        {
          title: "기기 사용방법",
          ic: require("@/assets/images/ic-equipment-20.svg"),
          seq: "001",
        },
        {
          title: "액세서리",
          ic: require("@/assets/images/ic-accessary-20.svg"),
          seq: "002",
        },
        {
          title: "고장/수리/오작동",
          ic: require("@/assets/images/ic-brokedown-20.svg"),
          seq: "003",
        },
        {
          title: "앱 사용",
          ic: require("@/assets/images/ic-app-20.svg"),
          seq: "004",
        },
        {
          title: "계정 및 회원 정보",
          ic: require("@/assets/images/ic-my-20.svg"),
          seq: "005",
        },
        {
          title: "구매/반품/환불",
          ic: require("@/assets/images/ic-box-20.svg"),
          seq: "006",
        },
        {
          title: "배송",
          ic: require("@/assets/images/ic-delivery-20.svg"),
          seq: "007",
        },
        {
          title: "기타",
          ic: require("@/assets/images/ic-etc-20.svg"),
          seq: "008",
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  activated() {
    this.active = true;
    if (!this.$route.meta.internalNavigation) {
      // 새로 들어왔을 경우
      if (!this.seq) {
        this.showToast("잘못된 경로입니다.");
        this.$router.go(-1);
      }
      this.tags.some((item) => {
        if (item.seq == this.seq) {
          this.selectTag = item;
          return true;
        }
      });
    } else {
      // 데이터만 갱신 (backkey 로 화면진입시)
    }
    this.$route.meta.internalNavigation = true;
  },
  mounted() {},
  destroyed() {},
  methods: {},
};
</script>

